.container {
  padding-left: 15px;
  padding-right: 15px;

  .page-header & {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
    margin: 0 auto;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1080px) {
  .container {
    width: 1080px;
  }
}
